@import url(https://fonts.googleapis.com/css2?family=Marcellus&display=swap);
.card{width:300px}.collection .collection-item img{width:150px;margin-right:25px}.collection-list{padding:0 3em;display:grid;grid-template-rows:auto 1fr auto;grid-gap:4em 2em}.collection-list h3{justify-self:center}.collection-list>div{justify-self:center;display:grid;grid-template-columns:repeat(3, 350px);grid-template-rows:550px auto;grid-column-gap:1em}.collection-list>div>div{background-color:#eee;transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s;cursor:pointer}.collection-list>div>div:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.collection-list>div>span{justify-self:center}.collection-list button{padding:.5em 1em;border:none;color:#fff;background-color:#000;justify-self:center}.collection-list button a{text-decoration:none;color:#fff}
body{margin:0;padding:0;line-height:1.4;box-sizing:border-box;font-family:"Marcellus",serif !important}.hide{display:none}.topHeader{background-color:#ffd9d2;color:#fff;text-shadow:1px 1px 2px #000;display:grid;grid-template-columns:1fr auto;padding:0 2em}.topHeader a{color:#fff;text-decoration:none}.topHeader a:hover{color:#333;text-decoration:none}.topHeader ul{margin:0;padding:0;list-style:none;display:grid;grid-gap:1em;grid-template-columns:repeat(7, auto)}.topNavbar{position:relative;z-index:2;display:grid;padding:0 2em;grid-template-columns:1fr auto auto}.topNavbar img{height:100px}.topNavbar:hover{background-color:#fff}.topNavbar:hover li:after{content:"";border-bottom:solid 1px #000;-webkit-transform:scaleX(0);transform:scaleX(0);transition:-webkit-transform 250ms ease-in-out;transition:transform 250ms ease-in-out;transition:transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out}.topNavbar:hover li:hover:after{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:0% 100%;transform-origin:0% 100%}.topNavbar:hover a{color:#333;text-shadow:none;font-weight:900}.topNavbar:hover a:hover{color:#ffd9d2;text-shadow:1px 1px 1px #333}.topNavbar ul{margin:0;padding:0;list-style:none;display:grid;grid-column-gap:3em;grid-template-columns:repeat(7, auto)}.topNavbar ul li{display:grid;font-family:"Marcellus";font-size:1em}.topNavbar ul li a{-webkit-align-self:center;align-self:center;text-decoration:none;color:#fff;text-shadow:1px 1px 2px #000}.topNavbar .hambuger{-webkit-align-self:center;align-self:center;cursor:pointer}.topNavbar .hambuger>span{z-index:3;display:block;width:25px;height:3px;margin:5px auto;transition:all .3s ease-in-out;background-color:gray}.topNavbar .nav-menu{z-index:5;position:fixed;top:0;right:0;width:400px;height:100vh;background:#fff !important}.topNavbar .nav-menu .icon-close{padding:20px;position:absolute;top:10px;right:15px;color:#000;cursor:pointer}.testing{min-width:100%;min-height:100vh}.carousel{min-width:100%;min-height:100vh;position:absolute;top:-100px}.carousel .carousel-item{width:100%;min-height:100vh;background:no-repeat center center scroll;background-size:cover}.footer{padding:2em 1em;display:grid;grid-template-columns:repeat(4, 250px);-webkit-justify-content:center;justify-content:center;justify-items:center;-webkit-align-items:start;align-items:start;grid-gap:1em}.footer .menu{display:grid;grid-template-rows:repeat(6, auto)}.footer .policy{display:grid;grid-template-rows:repeat(4, auto)}.footer .subscribe{display:grid;grid-template-rows:repeat(3, auto)}.footer .socials{display:grid;grid-template-rows:repeat(2, auto)}.overlay{position:fixed;z-index:4;top:0;left:0;background:rgba(0,0,0,.5);width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.subscribe-modal{position:fixed;top:30vh;left:30vw;z-index:6;background-color:#000;color:#fff;max-width:500px;min-height:300px;max-height:75vh;border-radius:5px;padding:3em 4em}.subscribe-modal .icon-close{position:absolute;padding:10px;top:10px;right:15px;color:#fff;cursor:pointer}.subscribe-modal form{width:100%;display:grid;grid-template-rows:repeat(5, auto);grid-gap:1em;justify-items:stretch;text-align:center}.subscribe-modal form :first-child{font-size:30px}.subscribe-modal form :last-child{font-size:10px;opacity:.5}@media screen and (max-width: 900px){.topNavbar li:not(:last-child){display:none}.topNavbar li:last-child{grid-column:-1}.topNavbar .menu{display:block;cursor:pointer}}@media screen and (max-width: 767px){.topHeader{font-size:3.5vw}.topHeader ul{display:none}}
