@import url('https://fonts.googleapis.com/css2?family=Marcellus&display=swap');
$pink: #FFD9D2;

body {
    margin: 0;
    padding: 0;
    line-height: 1.4;
    box-sizing: border-box;
    font-family: 'Marcellus', serif !important;
}
.hide {
    display: none;
}
.topHeader {
    background-color: $pink;
    color: #FFF;
    text-shadow: 1px 1px 2px #000;
    display: grid;
    grid-template-columns: 1fr auto;
    padding: 0 2em;
    & a {
        color: #FFF;
        text-decoration: none;
        &:hover {
            color: #333;
            text-decoration: none;
        }
    }
    & ul {
        margin: 0;
        padding: 0;
        list-style: none;
        display: grid;
        grid-gap: 1em;
        grid-template-columns: repeat(7, auto);
    }
}
.topNavbar {
    position: relative;
    z-index: 2;
    display: grid;
    padding: 0 2em;
    grid-template-columns: 1fr auto auto;
    & img {
        height: 100px;
    }
    &:hover {
        background-color: #FFF;
        & li:after {
            content: '';
            border-bottom: solid 1px #000;  
            transform: scaleX(0);  
            transition: transform 250ms ease-in-out;
        }
        & li:hover:after { 
            transform: scaleX(1); 
            transform-origin:  0% 100%;
        }

        & a {
            color: #333;
            text-shadow: none;
            font-weight: 900;
            &:hover {
                color: $pink;
                text-shadow: 1px 1px 1px #333;
            }
        }
    }
    & ul {
        margin: 0;
        padding: 0;
        list-style: none;
        display: grid;
        grid-column-gap: 3em;
        grid-template-columns: repeat(7, auto);
        & li {
            display: grid;
            font-family: 'Marcellus';
            font-size: 1em;
            & a {
                align-self: center;
                text-decoration: none;
                color: #FFF;
                text-shadow: 1px 1px 2px #000;
            }
        }
    }
    & .hambuger {
        align-self: center;
        cursor: pointer;
    }
    & .hambuger > span {
        z-index: 3;
        display: block;
        width: 25px;
        height: 3px;
        margin: 5px auto;
        transition: all 0.3s ease-in-out;
        background-color: grey;
    }
    & .nav-menu {
        z-index: 5;
        position: fixed;
        top: 0;
        right: 0;
        width: 400px;
        height: 100vh;
        background: #FFF !important;
        .icon-close {
            padding: 20px;
            position: absolute;
            top: 10px;
            right: 15px;
            color: #000;
            cursor: pointer;
        }
    }
}
.testing {
    min-width: 100%;
    min-height: 100vh;
}
.carousel {
    min-width: 100%;
    min-height: 100vh;
    position: absolute;
    top: -100px;
    & .carousel-item {
        width: 100%;
        min-height: 100vh;
        background: no-repeat center center scroll;
        background-size: cover;
    }
}
.footer {
    padding: 2em 1em;
    display: grid;
    grid-template-columns: repeat(4, 250px);
    justify-content: center;
    justify-items: center;
    align-items: start;
    grid-gap: 1em;
    & .menu {
        display: grid;
        grid-template-rows: repeat(6, auto);
    }
    & .policy {
        display: grid;
        grid-template-rows: repeat(4, auto);
    }
    & .subscribe {
        display: grid;
        grid-template-rows: repeat(3, auto);
    }
    & .socials {
        display: grid;
        grid-template-rows: repeat(2, auto);
    }
}
.overlay {
    position: fixed;
    z-index: 4;
    top: 0;
    left: 0;
    background:rgba(0,0,0,0.5);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.subscribe-modal {
    position: fixed;
    top: 30vh;
    left: 30vw;
    z-index: 6;
    background-color: #000;
    color: #FFF;
    max-width: 500px;
    min-height: 300px;
    max-height: 75vh;
    border-radius: 5px;
    padding: 3em 4em;
    .icon-close {
        position: absolute;
        padding: 10px;
        top: 10px;
        right: 15px;
        color: #FFF;
        cursor: pointer;
    }
    & form {
        width: 100%;
        display: grid;
        grid-template-rows: repeat(5, auto);
        grid-gap: 1em;
        justify-items: stretch;
        text-align: center;
        & :first-child {
            font-size: 30px;
        }
        & :last-child {
            font-size: 10px;
            opacity: 0.5;
        }
    }
}

@media screen and (max-width: 900px) {
    .topNavbar {
        & li:not(:last-child) {
            display: none;
        }
        & li:last-child {
            grid-column: -1;
        }
        & .menu {
            display: block;
            cursor: pointer;
        }
    }
}
@media screen and (max-width: 767px) {
    .topHeader {
        font-size: 3.5vw;
        & ul {
            display: none;
        }
    }
    
}