.card {
    width: 300px;
}
.collection .collection-item img{
    width: 150px;
    margin-right: 25px;
}

.collection-list {
    padding: 0 3em;
    display: grid;
    grid-template-rows: auto 1fr auto;
    grid-gap: 4em 2em;
    & h3 {
      justify-self: center;
    }
    & > div {
      justify-self: center;
      display: grid;
      grid-template-columns: repeat(3, 350px);
      grid-template-rows: 550px auto;
      grid-column-gap: 1em;
      & > div {
        background-color: #EEE;
        transition: transform .2s;
        cursor: pointer;
        &:hover {
          transform: scale(1.05);
        }
      }
      & > span {
        justify-self: center;
      }
    }
    & button {
      padding: 0.5em 1em;
      border: none;
      color: #FFF;
      background-color: #000;
      justify-self: center;
      & a {
        text-decoration: none;
        color: #FFF;
      }
    }
}